@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700;800&family=Work+Sans:wght@100;200;300;400;500;600;700;800&display=swap');
.container{
    margin: 0;
    height: 90vh;
    background-color: #F4F8FA;
    h1{
        margin: 0;
        padding: 2vw;
        font-family: 'Work Sans';
        font-style: normal;
        font-weight: 400;
        font-size: 25px;
        text-align: center;
        color: #1B31A8;
    }
    .inputform{
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        height: 50vh;
        width: 50vw;
        left: 15%;
    }
    button{
        position: absolute;
        width: 141px;
        height: 62px;
        left: 40%;
        top: 43%;
        border: none;
        background: #1B31A8;
        border-radius: 32px;
        color: #FFFFFF;
        font-size: 16px;
    }
    input{
        height: 56px;
        width: 272px;
        border-radius: 4px;
        border: none;
    }
    label{
        font-family: 'Work Sans';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 150%;
    }
    .distancetext{
        position: relative;
        height: 70px;
        width: 500px;
        display: flex;
        justify-content: space-around;
        background-color: #FFFFFF;
        font-family: 'Work Sans';
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        left: 15%;
        border-radius: 0.8rem 0.8rem 0 0;
        .numdis{
            font-weight: 900;
            color: #1B31A8;
        }
    }
    .distxtsub{
        position: relative;
        height: 70px;
        width: 500px;
        display: flex;
        justify-content: space-around;
        font-family: 'Work Sans';
        font-style: normal;
        font-weight: 400;
        font-size: 15px;
        left: 15%;
        border-radius:0 0 0.8rem 0.8rem;
        background-color: #e4e4e4;
    }
    
    .calc{
        display: grid;
        grid-template-columns: 1fr 1fr;
        .right{
            display: flex;
            justify-content: center;
            align-items: center;
            width: 50vw;
            height: 70vh;

            .box{
                height: 55vh;
                width: 55vh;
                background-color: white;
                box-shadow: 0px 0px 25px 8px rgb(226, 226, 226);
            }
        }
    }
}